@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700&family=Quicksand:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Radio+Canada&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


footer{
    display: flex;
    align-items: center;
    background-color: rgba(35, 47, 56, 0.5);
    padding: 10px;
}

.Footer-title{
    font-size: 2rem;
    display: inline;
    font-family: 'Raleway', sans-serif;
    padding: 8px 15px;
}

.Footer-title + span{
    padding: 6px 10px;
    border-left: 2px solid grey;
}

.Footer-link{
    color: black;
    cursor: pointer;
    display: inline;
    font-size: 2rem;
    padding: 5px;
    margin-left: auto;
}
header{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70vh;
    position: relative;
}

header::before{
    content: "";
    position: absolute;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    opacity: 0.87;
}

.Header-title{
    font-family: 'Montserrat', sans-serif;
    font-size: 3.8rem;
    text-align: center;
}
/* Styling Navbar */
#navbar {
    position: fixed;
    width: 100%;
    top: 0;
    padding: 10px;
    display: flex;
    z-index: 1;
    align-items: center;
    justify-content: space-around;
    transition: 0.5s;
}

.Navbar-title {
    font-family: 'Raleway', sans-serif;
    font-size: xx-large;
    color: white;
    padding: 8px;
}

/* Styling Navigation bar */
.navigation {
    display: flex;
}

.navigation-tabs{
    opacity: 0.7;
    padding: 6px 12px;
    cursor: pointer;
    transition: 0.2s;
}
.navTabTitle {
    display: inline-block;
    font-family: "Open Sans", sans-serif;
    font-size: 1.1em;
    white-space: nowrap;
    color: white;
    font-weight: 500;
    text-decoration: none;
    transition: 0.2s;
}


.navigation-tabs:hover{
    opacity: 1;
}

.navigation-tabs:hover .navTabTitle,.navigation-tabs:hover .navTabBottom{
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
}

.navigation-tabs:hover .navTabBottom div{
   max-width: 50%;
}

.navTabBottom{
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s;
}

.navTabBottom div{
    background-color: white;
    height: 2px;
    width: 50%;
    max-width: 0;
    transition: all 0.2s;
}

/* Styling User Button */
.nav-user-button {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 5px;
}

#nav-user-bt-icon{
    opacity: 0.7;
    padding: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 10px;
    background-color: rgba(171, 209, 207, 0.616);
}

#nav-user-bt-icon i{
    font-size: 1.7rem;
}

.nav-user-button .tooltip {
    opacity: 0;
    position: absolute;
    background-color: white;
    border-radius: 10px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding: 3px 15px;
    color: black;
    text-align: center;
    top: 90%;
    transition: 300ms;
    z-index: -1;
}

.nav-user-button .tooltip::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent white transparent;
}

#nav-user-bt-icon:hover + .tooltip {
    opacity: 0.7;
    top: calc(100% - -5px);
}

#nav-user-bt-icon:hover{
    opacity: 1;
}
#home {
    display: flex;
    flex-direction: column;
    padding: 10px;
    justify-content: center;
    align-items: center;
    margin: 20px;
}

.top-mid-container {
    display: inline;
    font-family: "Work Sans", sans-serif;
    padding: 8px 18px;
    text-align: center;
    box-shadow: 0px 0px 3px 1px black;
}

.blogs-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 25px;
}

.home-primary-container{
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 10px;
}
.blog-container{
    height: 325px;
    width: 280px;
    /* border: 2px solid black; */
    box-shadow: 0px 0px 5px 0px black;
    margin: 10px;
    transition: 0.3s;
}

.blog-image, .blog-image img{
    width: 100%;
}
.blog-image img{
    object-fit: cover;
    height: 100%;
}

.blog-image{
    overflow: hidden;
    height: 55%;
}

.blog-description{
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
   -webkit-line-clamp: 2;
   -webkit-box-orient: vertical;

}

.blog-info{
    font-family: 'Quicksand', sans-serif;
    font-size: 1.1em;
    padding: 8px;
}

.blog-title{
    font-family: 'Nunito', sans-serif;
    font-size: 1.5em;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
   -webkit-line-clamp: 1;
   -webkit-box-orient: vertical;
}


.blog-author-info{
    display: flex;
    white-space: nowrap;
}

.author-img{
    display: flex;
    justify-content: center;
}

.author-img img{
    border-radius: 50%;
    padding: 5px;
    padding-left: 0;
    height: 50px;
    
}
.blog-container:hover{
    cursor: pointer;
    box-shadow: 0px 2px 9px 1px black;
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
}
.catogories-div-primary{
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: center;
    padding: 50px;
}

.catogories{
    position: relative;
    height: 130px;
    width: 275px;
    border-radius: 8px;
    margin: 18px;
    cursor: pointer;
    box-shadow: 0px 0px 5px 1px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: 0.5s;
}

.catogories-image{
    width: 100%;
    height: 100%;
    opacity: 0.8;
    object-fit: cover;
    transition: 0.5s;
}

.catogories-title{
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    font-size: 1.4rem;
    position: absolute;
    transition: 0.5s;
}

.catogories:hover .catogories-image{
    opacity: 1;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
#about-me{
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-primary-container{
    display: flex;
    background-color: rgb(189, 182, 182);
    width: 85%;
    margin: 50px;
}

.about-pic{
    width: 300px;
    height: 300px;
    min-width: 300px;
    padding: 20px;
    object-fit: contain;
}

.about-pic img{
    width: 100%;
    height: 100%;
}

.about-details{
    font-family: 'Radio Canada', sans-serif;
    padding: 20px 20px 20px 0px;
}

.about-name{
    font-size: 2em;

}

.about-desc{
    font-size: 1.2em;
}
#contact-us{
    display: flex;
}

.contact-info{
    width: 40%;
    padding: 25px;
    background-color: rgb(189, 189, 189);
}

.contact-info h1{
    font-size: 2rem;
    padding: 8px 0px;
}

.contact-info div{
    font-size: 1.3rem;
}

/* Styling Contact form */
.contact-form-div{
    font-family: 'Poppins', sans-serif;
    margin: 20px;
    margin-bottom: 80px;
    margin-left: 40px;
    width: 60%;
    display: flex;
}

.form-input-div{
    display: flex;
    flex-direction: column-reverse;
}

#contact-form{
    width: 55%;
}

.form-input-div label{
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
}

.form-input-div input, textarea{
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
    padding: 8px;
    outline: none;
    border: 1.5px solid black;
}

.form-input-div input:focus,textarea:focus{
    border: 1.5px solid rgba(230, 165, 47, 0.788);
}

#contact-form-submit-bt{
    padding: 10px 18px;
    font-size: 1em;
    font-family: 'Poppins', sans-serif;
    margin: 8px 0px;
    cursor: pointer;
    color: white;
    border: none;
    border-radius: 2px;
    background-color: rgba(230, 150, 1, 0.9);
}

#contact-form-submit-bt:Hover{
    background-color: rgb(230, 150, 0);
}
.popup-primary-container {
    transition: 300ms;
}

.popup-primary-container::before {
    content: "";
    height: 100vh;
    width: 100vw;
    background-color: black;
    position: fixed;
    opacity: 0.5;
    z-index: 10;
    top: 0;
}

.popup-secondary-container {
    font-family: 'Ubuntu', sans-serif;
    width: 35%;
    position: fixed;
    z-index: 10;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    left: 50%;
    top: 50%;
    transition: 100ms;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-animation: popup-message-animation 100ms linear;
            animation: popup-message-animation 100ms linear;
}

#loading-icon {
    font-size: 3rem;
    -webkit-animation-name: loading-animation;
            animation-name: loading-animation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0ms;
            animation-delay: 0ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    display: block;
    
    height: -webkit-fit-content;
    
    height: -moz-fit-content;
    
    height: fit-content;
}

@-webkit-keyframes loading-animation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes loading-animation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes popup-message-animation {
    from {
        -webkit-transform: translate(-50%, -65%);
                transform: translate(-50%, -65%);
        opacity: 0;
    }
    to {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        opacity: 1;
    }
}

@keyframes popup-message-animation {
    from {
        -webkit-transform: translate(-50%, -65%);
                transform: translate(-50%, -65%);
        opacity: 0;
    }
    to {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        opacity: 1;
    }
}

.popup-message-sign i {
    font-size: 13rem;
    padding: 8px;
}

.popup-message-sign {
    text-align: center;
}

.popup-close-button {
    position: absolute;
    right: 20px;
    top: 15px;
    cursor: pointer;
}

.popup-close-button i {
    font-size: 2.2rem;
}

